.main-link-btn {
  box-sizing: border-box;
  display: block;
  max-width: 17.5rem;
  height: 2.625em;
  margin: 0 auto;
  padding: 12px;
  border-radius: 50px;
  background-color: #000000;
  white-space: nowrap;
  line-height: 1.5;
  transition: all 0.5s ease;
}

.main-link-btn:hover,
.main-link-btn :active {
  background-color: #424242;
  box-shadow: 0 0 20px -5px rgba(0, 0, 0, 0.5);
}

.go-to-top {
  display: block;
  position: fixed;
  z-index: 990;
  bottom: 0;
  width: 1.875em;
  height: 100%;
  transition: all 0.1s ease;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-name: fadeInUp;
}

@media screen and (min-width: 48rem) {
  .go-to-top {
    width: 3.75em;
  }
}

@keyframes fadeInUp {
  from {
    bottom: -200px;
  }
  to {
    bottom: 0;
  }
}

.go-to-top__arrow {
  position: absolute;
  left: 0.1875em;
  bottom: 1.875em;
  transform: rotate(-90deg);
  transition: all 0.5s ease;
  fill: #828282;
  animation: blink5s2sinft 5s 2s infinite both;
}

@media screen and (min-width: 48rem) {
  .go-to-top__arrow {
    left: 50%;
    margin-left: -12px;
  }
}

@keyframes blink5s2sinft {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

.go-to-top__arrow:hover {
  fill: #070707;
}

.go-to-top:hover .go-to-top__arrow {
  fill: #070707;
}

.go-to-top:hover {
  background-color: rgba(130, 130, 130, 0.2);
}

.burger-container {
  position: fixed;
  z-index: 999;
  right: 0.9375rem;
  top: 1.25em;
  display: inline-block;
  width: 2.1875rem;
  height: 100%;
  cursor: pointer;
  transform: rotate(0deg);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

