  .burger-container {
    display: none;
  }
}

.burger-container .burger-icon {
  width: 35px;
  height: 20px;
  position: relative;
  display: block;
  margin: -4px auto 0;
  top: 1%;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-name: fadeInLeft;
}

@keyframes fadeInLeft {
  from {
    transform: translateX(200px);
  }
  to {
    transform: translateX(0);
  }
}

.burger-container .burger-icon__bar {
  width: 100%;
  height: 0.25em;
  position: relative;
  background-color: #828282;
  transition: all 0.5s ease;
}

.burger-container .burger-icon__topBar {
  transform: translateY(0px) rotate(0deg);
  transition: all 0.2s ease;
}

.burger-container .burger-icon__cenBar {
  transform: translateY(6px) rotate(0deg);
}

.burger-container .burger-icon__botBar {
  transform: translateY(12px) rotate(0deg);
}

.burger-container .burger-icon:hover .burger-icon__bar {
  background-color: #070707;
}

.burger-container.burger-close .burger-icon__bar {
  background-color: #828282;
}

.burger-container.burger-close .burger-icon__topBar {
  height: 0;
  transform: translateY(0px) rotate(0deg);
  transition: all 0.2s ease;
}

.burger-container.burger-close .burger-icon__cenBar {
  transform: translateX(-2px) translateY(10px) rotate(45deg);
  width: 39px;
}

.burger-container.burger-close .burger-icon__botBar {
  transform: translateX(-2px) translateY(6px) rotate(-45deg);
  width: 39px;
}

